'use client';

import { useAuth } from '@/lib/contexts/AuthContext';
import Navbar from '@/components/Navbar';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';

export default function AdminPage() {
    const { user, loading } = useAuth();
    const router = useRouter();

    useEffect(() => {
        if (!loading && (!user || user.role !== 'admin')) {
            router.push('/');
        }
    }, [user, loading]);

    if (loading) return <div className="p-20 text-center">Loading...</div>;
    if (!user || user.role !== 'admin') return null;

    return (
        <div className="min-h-screen bg-gray-50">
            <Navbar />
            <div className="max-w-7xl mx-auto px-4 py-12">
                <h1 className="text-4xl font-black text-gray-900 mb-10">Admin Dashboard</h1>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mb-12">
                    {[
                        { label: 'Total Sales', value: '$12,450', color: 'bg-green-100 text-green-600', icon: 'üí∞' },
                        { label: 'Orders', value: '156', color: 'bg-blue-100 text-blue-600', icon: 'üì¶' },
                        { label: 'Products', value: '42', color: 'bg-purple-100 text-purple-600', icon: 'üì±' },
                        { label: 'Customers', value: '89', color: 'bg-orange-100 text-orange-600', icon: 'üë•' },
                    ].map((stat, i) => (
                        <div key={i} className="bg-white p-8 rounded-3xl shadow-sm border border-gray-100">
                            <div className="flex justify-between items-start mb-4">
                                <span className="text-2xl">{stat.icon}</span>
                                <span className={`px-2 py-1 rounded-lg text-xs font-black ${stat.color}`}>+12%</span>
                            </div>
                            <p className="text-gray-500 font-bold text-sm uppercase mb-1">{stat.label}</p>
                            <p className="text-3xl font-black text-gray-900">{stat.value}</p>
                        </div>
                    ))}
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
                    <div className="bg-white p-8 rounded-3xl shadow-sm border border-gray-100">
                        <h2 className="text-2xl font-bold mb-6">Quick Actions</h2>
                        <div className="grid grid-cols-2 gap-4">
                            <Link href="/admin/products/new" className="p-6 bg-purple-50 hover:bg-purple-100 rounded-2xl text-center transition group">
                                <div className="text-3xl mb-2 group-hover:scale-110 transition">‚ûï</div>
                                <p className="font-bold text-purple-700">Add Product</p>
                            </Link>
                            <Link href="/admin/categories" className="p-6 bg-blue-50 hover:bg-blue-100 rounded-2xl text-center transition group">
                                <div className="text-3xl mb-2 group-hover:scale-110 transition">üìÅ</div>
                                <p className="font-bold text-blue-700">Categories</p>
                            </Link>
                            <Link href="/admin/orders" className="p-6 bg-green-50 hover:bg-green-100 rounded-2xl text-center transition group">
                                <div className="text-3xl mb-2 group-hover:scale-110 transition">üöö</div>
                                <p className="font-bold text-green-700">Manage Orders</p>
                            </Link>
                            <Link href="/admin/users" className="p-6 bg-orange-50 hover:bg-orange-100 rounded-2xl text-center transition group">
                                <div className="text-3xl mb-2 group-hover:scale-110 transition">üõ°Ô∏è</div>
                                <p className="font-bold text-orange-700">User Setup</p>
                            </Link>
                        </div>
                    </div>

                    <div className="bg-white p-8 rounded-3xl shadow-sm border border-gray-100">
                        <h2 className="text-2xl font-bold mb-6">Recent Orders</h2>
                        <div className="space-y-4">
                            {[1, 2, 3].map((_, i) => (
                                <div key={i} className="flex items-center justify-between p-4 bg-gray-50 rounded-2xl">
                                    <div className="flex items-center gap-4">
                                        <div className="w-10 h-10 bg-white rounded-lg flex items-center justify-center font-bold text-xs">#RD</div>
                                        <div>
                                            <p className="font-bold text-sm">Order #12345</p>
                                            <p className="text-xs text-gray-500">2 mins ago</p>
                                        </div>
                                    </div>
                                    <p className="font-black text-purple-600">$129.00</p>
                                </div>
                            ))}
                            <Link href="/admin/orders" className="block text-center text-sm font-bold text-gray-500 hover:text-purple-600 transition mt-4">View All Orders ‚Üí</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
